import React from "react";
import axios from "axios";
import Movie from "./Movie";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async () => {
    const movies = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating");
    this.setState({movies: movies.data.data.movies, isLoading: false});
  }
  componentDidMount(){
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return <section className="container">
      {isLoading ? <div className="loader">
        <span class="loader_text">"Loading..."</span>
        </div>: <div className="movies">{
        movies.map(movie => (
       <Movie 
          key={movie.id} 
          id={movie.id} year={movie.year} 
          title={movie.title} 
          summary={movie.summary} 
          poster={movie.medium_cover_image} 
          genres={movie.genres}/>
    ))}
      </div>}</section>
  }
}
export default App;
